import angular from 'angular';

import {FactoryCreator} from 'angular-es-utils';

import utils from '../../common/utils';

import loading from '../loading';
import dynamicAttr from '../dynamic-attr';
import checkbox from '../checkbox';

import GridDirective from './GridDirective';
import GridCell from './grid-cell';
import {$Grid, GridTemplates} from './GridService';

export default angular
	.module('ccms.components.grid', [utils, loading, dynamicAttr, checkbox])
	.directive('grid', FactoryCreator.create(GridDirective))
	.directive('gridCell', FactoryCreator.create(GridCell))
	.service('$grid', $Grid)
	.constant('GRID_TEMPLATES', GridTemplates)
	.name;



