export default class TooltipCtrl {

	constructor() {
		this.tooltip = null;
		this.opened = false;
	}

	toggle() {
		this[(this.opened = !this.opened) ? 'open' : 'close']();
	}

	open() {
		this.tooltip.open();
		this.opened = true;
	}

	close() {
		this.tooltip.close();
		this.opened = false;
	}

}



