import angular from 'angular';
import angularUtils from '../../common/utils';
import {FactoryCreator} from 'angular-es-utils';

import Pagination from './Paginantion';
import ngEnter from '../ng-enter';

export default angular
	.module('ccms.components.pagination', [angularUtils, ngEnter])
	.directive('pagination', FactoryCreator.create(Pagination))
	.name;



