import './_grid.scss';

import GridCtrl from './GridCtrl';
import template from './tpls/grid.tpl.html';

export default class GridDirective {

	constructor() {

		Object.assign(this, {

			restrict: 'E',
			template,
			controller: GridCtrl,
			controllerAs: '$ctrl',
			scope: true

		});

	}

}



